import { configureStore, createSlice } from "@reduxjs/toolkit";

import recordData from "./store/recordDataSlice.js";

import users from "./store/usersSlice.js";

//년도 데이터
let years = createSlice({
    name: "years",
    initialState: [
        { id: 0, year: "2020" },
        { id: 1, year: "2021" },
        { id: 2, year: "2022" }
    ]
});

export default configureStore({
    reducer: {
        users: users.reducer,
        recordData: recordData.reducer,
        years: years.reducer
    }
});
