import { useState, useEffect } from "react";
import { Table, Form } from "react-bootstrap";
import { BrowserRouter, Routes, Route, Link, useNavigate, Outlet } from "react-router-dom";

//mui 라이브러리
import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Container from "@mui/material/Container";

//redux 라이브러리
import { useSelector, useDispatch } from "react-redux";
//redux  데이터
import { recordFilter, recordSort } from "../../../redux/store/recordDataSlice.js";

function RecordList() {
    let dispatch = useDispatch();
    let navigate = useNavigate();

    let users = useSelector(state => {
        return state.users;
    });
    let recordData = useSelector(state => {
        let aaa = state.recordData;
        console.log("기록데이터", aaa);
        return state.recordData;
    });

    let years = useSelector(state => {
        return state.years;
    });

    const [season, setSeason] = useState("");
    let [currentYear, setCurrentYear] = useState("");

    useEffect(() => {
        const now = new Date(); // 현재 날짜 및 시간
        let YYYY = now.getFullYear(); // 연도
        setSeason("");

        dispatch(recordFilter({ type: "SEARCH_SEASON", year: "" }));

        return () => {
            setSeason("");
        };
    }, []);

    //정렬 함수 sort 사용해서 총점 기준으로 정렬
    let sortRecordData = [...recordData].sort(function(a, b) {
        return b.totalPoint - a.totalPoint;
    });

    //배열 객체 중복 체크
    const arrSeason = recordData.filter((character, idx, arr) => {
        return arr.findIndex(item => item.season === character.season) === idx;
    });

    //정렬  th 두번 클릭
    let [thClickCount, setThClickCount] = useState(0);

    //오름차순 내림차순  펑션
    let thClickFun = () => {
        let getType;
        if (thClickCount <= 0) {
            //내림차수
            getType = "DESC";
            setThClickCount(thClickCount + 1);
        } else if (thClickCount <= 1) {
            //오름차수
            getType = "ASC";
            setThClickCount(thClickCount - 1);
        }
        return getType;
    };

    return (
        <Container fixed>
            <br></br>
            <Box sx={{ minWidth: 120 }}>
                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">시즌</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={season}
                        label="시즌"
                        onChange={e => {
                            e.stopPropagation();
                            setSeason(e.target.value);

                            dispatch(recordFilter({ type: "SEARCH_SEASON", year: e.target.value }));
                        }}
                    >
                        {years.map(function(e, i) {
                            return <MenuItem value={years[i].year}>{years[i].year}</MenuItem>;
                        })}
                    </Select>
                </FormControl>
            </Box>

            <Table striped bordered hover variant="dark">
                <thead>
                    <tr>
                        <th>#</th>
                        <th
                            onClick={e => {
                                e.stopPropagation();

                                dispatch(recordSort({ type: "SORT_BACKNO", div: thClickFun() }));
                            }}
                        >
                            등번호
                        </th>
                        <th
                            onClick={e => {
                                e.stopPropagation();
                                dispatch(recordSort({ type: "SORT_NAME", div: thClickFun() }));
                            }}
                        >
                            이름
                        </th>
                        <th
                            onClick={e => {
                                e.stopPropagation();
                                console.log(111);
                                dispatch(recordSort({ type: "SORT_GAMES", div: thClickFun() }));
                            }}
                        >
                            경기수
                        </th>
                        <th
                            onClick={e => {
                                e.stopPropagation();
                                console.log(111);
                                dispatch(recordSort({ type: "SORT_GOALS", div: thClickFun() }));
                            }}
                        >
                            골
                        </th>
                        <th
                            onClick={e => {
                                e.stopPropagation();
                                console.log(111);
                                dispatch(recordSort({ type: "SORT_ASSIST", div: thClickFun() }));
                            }}
                        >
                            어시스트
                        </th>
                        <th
                            onClick={e => {
                                e.stopPropagation();
                                console.log(111);
                                dispatch(recordSort({ type: "SORT_TOTALPOINT", div: thClickFun() }));
                            }}
                        >
                            총점
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {/* {users.map(function(e, i) {
                        return users[i].id == recordData[i].id ? (
                            <tr>
                                <td>{i + 1}</td>
                                <td>{users[i].backNo}</td>
                                <td>{users[i].name}</td>
                                <td>{recordData[i].games}</td>
                                <td>{recordData[i].score}</td>
                                <td>{recordData[i].assist}</td>
                                <td>20</td>
                            </tr>
                        ) : null;
                    })} */}
                    {recordData.map(function(e, i) {
                        return (
                            <tr
                                onClick={() => {
                                    navigate("/PlayerInfo/" + recordData[i].id);
                                }}
                            >
                                <td>{i + 1}</td>
                                <td>{recordData[i].backNo}</td>
                                <td>{recordData[i].name}</td>
                                <td>{recordData[i].games}</td>
                                <td>{recordData[i].goals}</td>
                                <td>{recordData[i].assist}</td>
                                <td>{recordData[i].totalPoint}</td>
                            </tr>
                        );
                    })}
                </tbody>
            </Table>
        </Container>
    );
}

export default RecordList;
