import { configureStore, createSlice } from "@reduxjs/toolkit";

//선수 시즌 정보
let recordData = createSlice({
    name: "recordData",
    initialState: [
        { id: 0, name: "문용철", backNo: 2, games: 20, goals: 1, assist: 2, totalPoint: 23, season: "2022" },
        { id: 2, name: "최대웅", backNo: 3, games: 10, goals: 2, assist: 2, totalPoint: 14, season: "2022" },
        { id: 2, name: "최대웅", backNo: 4, games: 30, goals: 1, assist: 2, totalPoint: 33, season: "2021" },
        { id: 1, name: "박진용", backNo: 1, games: 30, goals: 1, assist: 2, totalPoint: 33, season: "2022" }
    ],
    reducers: {
        //시즌별 조회
        recordFilter(state, action) {
            const type = action.payload.type;
            const year = action.payload.year;
            let todo = [...state];

            if (year != "") {
                switch (type) {
                    case "SEARCH_SEASON":
                        return todo.filter(function(e) {
                            return e.season == year;
                        });
                        break;
                    default:
                        return todo.sort(function(a, b) {
                            return b.totalPoint - a.totalPoint;
                        });
                }
                // if (type == "SEARCH_SEASON") {
                //     return todo.filter(function(e) {
                //         return e.season == year;
                //     });

                //     //정렬 함수 sort 사용해서 총점 기준으로 정렬
                //     // return filterData.sort(function(a, b) {
                //     //     return b.totalScore - a.totalScore;
                //     // });
                // } else {
                //     return todo.sort(function(a, b) {
                //         return b.totalPoint - a.totalPoint;
                //     });
                // }
            }
        },
        //리스트 목록 클릭시 오름차순 내림차순 정렬
        recordSort(state, action) {
            console.log("리듀서가 일을한다!", action);
            console.log(state);
            const type = action.payload.type;
            const div = action.payload.div;
            let todo = [...state];
            let sortData;
            switch (type) {
                case "SORT_BACKNO":
                    if (div == "DESC") {
                        sortData = todo.sort((a, b) => a.backNo - b.backNo);
                    } else if (div == "ASC") {
                        sortData = todo.sort((a, b) => b.backNo - a.backNo);
                    }
                    break;
                case "SORT_NAME":
                    if (div == "DESC") {
                        sortData = todo.sort((a, b) => (a.name < b.name ? -1 : a.name > b.name ? 1 : 0));
                    } else if (div == "ASC") {
                        sortData = todo.sort((a, b) => (b.name < a.name ? -1 : b.name < a.name ? 1 : 0));
                    }
                    break;
                case "SORT_GAMES":
                    if (div == "DESC") {
                        sortData = todo.sort((a, b) => a.games - b.games);
                    } else if (div == "ASC") {
                        sortData = todo.sort((a, b) => b.games - a.games);
                    }
                    break;
                case "SORT_GOALS":
                    if (div == "DESC") {
                        sortData = todo.sort((a, b) => a.goals - b.goals);
                    } else if (div == "ASC") {
                        sortData = todo.sort((a, b) => b.goals - a.goals);
                    }
                    break;
                case "SORT_ASSIST":
                    if (div == "DESC") {
                        sortData = todo.sort((a, b) => a.assist - b.assist);
                    } else if (div == "ASC") {
                        sortData = todo.sort((a, b) => b.assist - a.assist);
                    }
                    break;
                case "SORT_TOTALPOINT":
                    if (div == "DESC") {
                        sortData = todo.sort((a, b) => a.totalPoint - b.totalPoint);
                    } else if (div == "ASC") {
                        sortData = todo.sort((a, b) => b.totalPoint - a.totalPoint);
                    }
                    break;
            }

            return sortData;
        },
        addrecord(state, action) {
            console.log("리듀서가 일을한다!", action);
            const type = action.payload.type;
            const recordData = action.payload.recordData;
            let todo = [...state];
            switch (type) {
                case "ADD_USERS_RECORD":
                    todo.push(recordData);

                    break;
            }
            console.log("기록 js !", todo);
            return todo;
        }
    }
});
export let { recordFilter, recordSort, addrecord } = recordData.actions;

export default recordData;
