import { useState, useEffect } from "react";
import Container from "@mui/material/Container";

//mui 라이브러리
import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";

//redux 라이브러리
import { useSelector, useDispatch } from "react-redux";
//redux  데이터
import { addrecord } from "../../../redux/store/recordDataSlice.js";
//redux  데이터
import { addPlayer } from "../../../redux/store/usersSlice";

function AddPlayerForm(props) {
    let dispatch = useDispatch();

    let years = useSelector(state => {
        return state.years;
    });

    let users = useSelector(state => {
        return state.users;
    });

    const [year, setYear] = useState("");
    const [foot, setFoot] = useState("");
    const [age, setAge] = useState("");

    let [seletAge, setSelectAge] = useState([]);
    let [seletYears, setSelectYears] = useState([]);
    let [currentYear, setCurrentYear] = useState("");
    let [currentId, setCurrentId] = useState();

    useEffect(() => {
        const now = new Date(); // 현재 날짜 및 시간
        let YYYY = now.getFullYear(); // 연도

        let arryAge = [...seletAge];
        let arryYears = [...seletYears];

        const maxId = users.reduce(function(prev, current) {
            return prev.id > current.id ? prev.id : current.id;
        }); //returns object

        setCurrentId(maxId + 1);

        //나이 데이터
        for (let i = 18; i <= 60; i++) {
            arryAge.push(i);
        }
        //년도 데이터
        for (let j = 2015; j <= YYYY; j++) {
            arryYears.push(j);
        }
        setSelectAge(arryAge);
        setSelectYears(arryYears);
        //return 이 먼저 실행된다.
        return () => {
            setSelectAge([]);
            setSelectYears([]);
            setCurrentId();
        };
    }, []);

    let forYear = () => {};

    return (
        <Container fixed>
            <div className="container">
                <div className="row">
                    <div className="col-md-6">
                        <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                    </div>
                    <div className="col-md-6">
                        <form
                            onSubmit={e => {
                                e.preventDefault();

                                if (
                                    e.target.name.value == "" ||
                                    e.target.age.value == "" ||
                                    e.target.year.value == "" ||
                                    e.target.backNo.value == "" ||
                                    e.target.body.value == "" ||
                                    e.target.foot.value == ""
                                ) {
                                    console.log("널이네");
                                    alert("정보를 넣어주세요");
                                    return;
                                } else {
                                    let userData = {
                                        id: currentId,
                                        name: e.target.name.value,
                                        age: e.target.age.value,
                                        joined: e.target.year.value,
                                        foot: e.target.foot.value,
                                        profile: e.target.body.value
                                    };
                                    let recordData = { id: currentId, name: e.target.name.value, backNo: e.target.backNo.value, games: 0, goals: 0, assist: 0, totalPoint: 0, season: currentYear };
                                    dispatch(addPlayer({ type: "ADD_USERS", userData: userData }));
                                    dispatch(addrecord({ type: "ADD_USERS_RECORD", recordData: recordData }));
                                }
                            }}
                        >
                            <h3 className="pt-5"></h3>
                            <div>
                                <h5>이름</h5>
                                <TextField id="outlined-basic" name="name" label="이름" variant="outlined" />
                            </div>
                            <br></br>
                            <div>
                                <h5>나이</h5>
                                <FormControl fullWidth>
                                    <InputLabel id="demo-simple-select-label">나이</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="demo-simple-select"
                                        name="age"
                                        value={age}
                                        label="나이"
                                        onChange={e => {
                                            setAge(e.target.value);
                                        }}
                                    >
                                        {seletAge.map(function(e, i) {
                                            return <MenuItem value={seletAge[i]}>{seletAge[i]}</MenuItem>;
                                        })}
                                    </Select>
                                </FormControl>
                            </div>
                            <br></br>
                            <div>
                                <h5>입단년도</h5>
                                <FormControl fullWidth>
                                    <InputLabel id="demo-simple-select-label">년도</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="demo-simple-select"
                                        name="year"
                                        value={year}
                                        label="년도"
                                        onChange={e => {
                                            setYear(e.target.value);
                                        }}
                                    >
                                        {seletYears.map(function(e, i) {
                                            return <MenuItem value={seletYears[i]}>{seletYears[i]}</MenuItem>;
                                        })}
                                    </Select>
                                </FormControl>
                            </div>
                            <br></br>
                            <div>
                                <h5>등번호</h5>
                                <TextField id="outlined-basic" name="backNo" label="등번호" variant="outlined" />
                            </div>
                            <br></br>
                            <div>
                                <h5>신체</h5>
                                <TextField id="outlined-basic" name="body" label="신체" variant="outlined" />
                            </div>
                            <br></br>
                            <div>
                                <h5>주발</h5>

                                <FormControl fullWidth>
                                    <InputLabel id="demo-simple-select-label">주발</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="demo-simple-select"
                                        value={foot}
                                        label="주발"
                                        name="foot"
                                        onChange={e => {
                                            setFoot(e.target.value);
                                        }}
                                    >
                                        <MenuItem value={"오른발"}>오른발</MenuItem>
                                        <MenuItem value={"왼발"}>왼발</MenuItem>
                                        <MenuItem value={"양발"}>양발</MenuItem>
                                    </Select>
                                </FormControl>
                            </div>
                            <br></br>
                            <div>
                                <Button type="submit" variant="contained" disableElevation className="col-md-11">
                                    저장
                                </Button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </Container>
    );
}

export default AddPlayerForm;
