import { configureStore, createSlice } from "@reduxjs/toolkit";

//선수 시즌 정보
let users = createSlice({
    name: "users",
    initialState: [
        { id: 0, name: "문용철", age: "35", joined: "2015", foot: "오른발", profile: "186/95" },
        { id: 1, name: "박진용", age: "36", joined: "2015", foot: "왼발", profile: "183/88" },
        { id: 2, name: "최대웅", age: "29", joined: "2015", foot: "양발", profile: "162/60" }
    ],
    reducers: {
        addPlayer(state, action) {
            console.log(action);
            const type = action.payload.type;
            const userData = action.payload.userData;
            let todo = [...state];

            switch (type) {
                case "ADD_USERS":
                    todo.push(userData);

                    break;
            }
            return todo;
        }
    }
});
export let { addPlayer } = users.actions;

export default users;
